}

body {
    margin: 0;
    background-color: #F5F5F5;
}

.nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #FE88A0;
    min-height: 10vh;
    font-size: 20px;
}

.nav-links {
    width: 30%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
    /* color: black; */
    text-decoration: none;
    cursor: pointer;
    /* font-size: 25px; */
}

.container {
    height: 500px;
    width: 100%;
    background-color: #FE88A0;
    /* position: relative; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.container h1 {
    font-size: 50px;
}

.container button {
    margin-top: 15px;
    height: 50px;
    width: 150px;
    font-size: 25px;
    border-radius: 5px;
    cursor: pointer;
    /* background-color: rgb(108, 108, 236); */
    background-color: #A1C2D1;
    font-weight: 100;
    border: none;
}

